import React from "react";
import Dialog from "@material-ui/core/Dialog";
import { withStyles, makeStyles } from "@material-ui/core/styles";
import MuiDialogActions from "@material-ui/core/DialogActions";
import MuiDialogContent from "@material-ui/core/DialogContent";
// import RedTriangle from "../../../ReusableComponents/Alert/Icons/RedTriangle.svg"
import { useTranslation } from "react-i18next";
import { Alert } from "@material-ui/lab";
import { BlueButton } from "../../GetButtons/GetButtons";

const useStyles = makeStyles((theme) => ({
  root: {
    padding: 0,
    margin: "4px",
    border: "1px solid #C4C4C4",
    "& .MuiChip-root": {
      borderRadius: 2,
      width: "35%",
      height: "25px",
      backgroundColor: "#F0F0F0",
    },
    "& .MuiButtonBase-root": {
      margin: "0 !important",
    },
    "& .MuiInputBase-input": {
      border: "none !important",
      width: "75% !important",
      flexGrow: "0",
    },
    "& .MuiAutocomplete-endAdornment": {
      right:
        window?.depsatkur_configs?.selectedLanguage === "ar" || window?.depsatkur_configs?.selectedLanguage === "ar-SA"
          ? "unset"
          : null,
      left:
        window?.depsatkur_configs?.selectedLanguage === "ar" || window?.depsatkur_configs?.selectedLanguage === "ar-SA"
          ? "-183px"
          : null,
    },
    "& .MuiChip-deleteIcon": {
      marginRight:
        window?.depsatkur_configs?.selectedLanguage === "ar" || window?.depsatkur_configs?.selectedLanguage === "ar-SA"
          ? "unset"
          : null,
      marginLeft:
        window?.depsatkur_configs?.selectedLanguage === "ar" || window?.depsatkur_configs?.selectedLanguage === "ar-SA"
          ? "5px"
          : null,
    },
  },
  tooltip: {
    maxWidth: "160px",
    background: "#FFFFFF 0% 0% no-repeat padding-box",
    color: "black",
    border: "#70707075 1px solid",
    boxShadow: "0px 3px 6px #00000029",
    opacity: 1,
    fontSize: 12,
  },
  dialogPaper: {
    minHeight: "200px",
    width: "500px",
    borderRadius: "2px",
    overflowX: "hidden",
  },
  dialogHeader: {
    fontWeight: "bold",
    padding: "10px 15px",
    fontSize: "14px",
  },
  dialogFooter: {
    position: "relative",
    display: "flex",
    "justify-content": "end",
    // padding: "10px",
    bottom: 0,
    width: "100%",
    "background-color": "#f0f0f0",
  },
  dialogContent: {
    width: "100%",
    justifyContent: "space-between",
    fontFamily: "Open Sans !important",
    fontSize: "14px !important",
  },
  dialogSubHeading: {
    width: "100%",
    height: "1px",
    backgroundColor: "#F0F0F0",
    fontSize: "12px",
    fontWeight: "bold",
    border: "1px solid #d7d7d7",
  },
  cancelButton: {
    cursor: "pointer",
    borderRadius: "3px",
    border: "1px solid grey",
    width: "60px",
    height: "30px",
    margin: "0px 10px",
    cursor: "pointer",
  },
  deleteButton: {
    cursor: "pointer",
    width: "60px",
    height: "30px",
    margin: "0px 10px",
    backgroundColor: "#D53D3D",
    color: "white",
    border: "none",
    borderRadius: "3px",
    cursor: "pointer",
  },
  backShade: {
    zIndex: "1201",
    height: "100%",
    width: "100%",
    background: "#00000067",
    position: "absolute",
    top: 0,
    left:"0"
  },
  createNew_PopUp: {
    display: "flex",
    flexDirection: "column",
    direction: window?.interfaceDesigner_configs?.selectedLanguage === "ar" || window?.interfaceDesigner_configs?.selectedLanguage === "ar-SA" ? "rtl" : "ltr",
    position: "absolute",
    zIndex: "1202",
    left: "50%",
    top: "10vh",
    // height:"35vh",width:"520px",
    // height:"240px",
    width: "422px",
    height: "352px",
    background: "#fff",
    border: "1px solid #00000040",
    transform: "translate(-50%,0)"
  },
  createNew_PopUp_Publish: {
    display: "flex",
    flexDirection: "column",
    direction: window?.interfaceDesigner_configs?.selectedLanguage === "ar" || window?.interfaceDesigner_configs?.selectedLanguage === "ar-SA" ? "rtl" : "ltr",
    position: "absolute",
    zIndex: "1202",
    left: "50%",
    top: "10vh",
    // height:"35vh",width:"520px",
    // height:"240px",
    width: "422px",
    height: "312px",
    background: "#fff",
    border: "1px solid #00000040",
    transform: "translate(-50%,0)"
  },
  createNew_PopUp_header: {
    borderBottom: "1px solid #00000040",
    textAlign: (style) => (style.translationStyles1 ? "right" : "left"),
    fontSize: "14px",
    fontWeight: "600",
    padding: "0 18px",
    height: "50px",
    lineHeight: "50px"
  },
  createNew_PopUp_body: {
    padding: "0px 18px",
    minHeight: "35vh",
    overflowY: "auto",
    height: "fit-content",
    backgroundColor: "white",
    "& div": {
      // display:"flex",
      margin: "7px 0",
      justifyContent: "space-between"
    },
    "& label": {
      fontSize: "12px",
      fontWeight: "600",
      color: "#606060",
      display: "block",
      textAlign: window?.interfaceDesigner_configs?.selectedLanguage === "ar" || window?.interfaceDesigner_configs?.selectedLanguage === "ar-SA"?"right":"left",
      // marginTop:"1em",
      minWidth: "100px",
      lineHeight: "28px"
    },
    "& input, & textarea": {
      display: "block",
      textAlign: window?.interfaceDesigner_configs?.selectedLanguage === "ar" || window?.interfaceDesigner_configs?.selectedLanguage === "ar-SA"?"right":"left",
      width: "-webkit-fill-available",
      width: "-moz-available",
      width: "100%",
      resize: "none",
      padding: "0 5px",
      fontSize: "12px",
      height: "28px",
      border: "1px solid #C4C4C4",
      borderRadius: "2px",
      // marginLeft:"5px",
      "&:focus , &:focus-visible , &:hover": {
        border: "1px solid #C4C4C4",
        outline: "none"
      }
    },
    "& textarea": {
      height: "95px"
    }
  },
  createNew_PopUp_footer: {
    textAlign: (style) => (style.translationStyles1 ? "left" : "right"),
    background: "#F5F5F5",
    boxSizing: "border-box",
    height: "50px",
    padding: "10px 10px",
    marginTop: "auto",
    "& button": {
      margin: "0 6px !important",
      fontSize: "12px",
      height: "28px",
      minWidth: "54px"
    }
  },
  errorMsg: {
    color: "red",
    fontSize: "12px",
    lineHeight: "20px",
    marginLeft: "7px !important"
  },
  inputWrapper: {
    flexDirection: "column",
    width: "-webkit-fill-available",
    width: "-moz-available"
  },
  dataObjectHeading: {
    background: "#F0F0F0 0% 0% no-repeat padding-box",
    height: "32px",
    width: "34vw",
    display: "flex",
    alignItems: "center",
    paddingLeft: "13px",
    fontSize: "12px",
    fontWeight: 600,
    margin: "7px 0px 0px 0px !important"
  },
  infoHeding: {
    background: "#E1B01114 0% 0% no-repeat padding-box",
    height: "32px",
    display: "flex",
    alignItems: "center",
    paddingLeft: "13px",
    fontSize: "11px"
  },
  mainHeding: {
    fontSize: "14px",
    height: "auto",
    display: "flex",
    alignItems: "center"
  },
  alertImg: {
    width: "27px",
    paddingLeft: "13px"
  },
  objectList: {
    fontSize: "12px",
    fontFamily: "Open Sans",
    paddingLeft: "10px"
  },
  objectWrap: {
    height: "32px",
    border: "1px solid #F0F0F0",
    margin: "0px !important",
    display: "flex",
    overflow: "hidden",
    alignItems: "center",
    justifyContent: "flex-start !important"
  },
  mainWrap: {
    height: "fit-content",
    overflowY: "auto",
    margin: "0px !important",
    marginBottom: "5px !important"
  },
  separator: {
    border: "1px solid #00000030",
    margin: "15px 0"
  }
}));
const DialogContent = withStyles((theme) => ({
  root: {
    padding: 0,
    fontFamily: "Open Sans",
    fontSize: "14px",
    // textAlign: "center",
    margin: "20px 15px",
  },
}))(MuiDialogContent);
const DialogActions = withStyles((theme) => ({
  root: {
    margin: 0,
    padding: 0,
  },
}))(MuiDialogActions);

const ShowDataList = (props) => {
  const classes = useStyles();

  const { t } = useTranslation(["styles", "translation"]);

  return (
    <Dialog
      open={props.dataListOpen}
      classes={{ paper: classes.dialogPaper }}
      className={"formBuilder_main_app"}
    >
      {/* heading section */}
      <div>
        <div className={classes.dialogHeader}>
          <span>{t("translation:Failed Data Object Mapping List")}</span>
        </div>
      </div>
      <div className={classes.dialogSubHeading}></div>
      <DialogContent>
        <span>
        {t("translation:Below Data Objects are not compatible which are associated with the selected Business Function")}
      </span> 
      
      <div className={classes.dataObjectHeading}>
            {t("translation:Data Objects Name")}
      </div>

      <div className={classes.mainWrap}>
                {props.dataList &&
                  props.dataList.length > 0 &&
                  props.dataList.map((item) => (
                <div className={classes.objectWrap}>
                    <Alert style={{width: "34vw", paddingTop: "15px"}} severity="warning">
                    <p className={classes.objectList}>{item}</p>
                </Alert>
            </div>
            ))}
        </div>
      </DialogContent>

      {/* footer section */}

      <DialogActions>
        <div className={`${classes.dialogFooter}`}>
            <BlueButton
            style={{width: "60px"}}
            onClick={() => props.setDataListOpen(false)}>
          {/* <button
            className={`${classes.deleteButton} danger`}
            onClick={() => props.setDataListOpen(false)}
          > */}
            {t("translation:OK")}
          {/* </button> */}
          </BlueButton>
        </div>
      </DialogActions>
    </Dialog>
  );
};

export default ShowDataList;
