import React, { useState, useRef, useEffect } from "react";
import { Card, Row, Form } from "react-bootstrap";
import ControlsMap from "../../../Properties/PropertiesConfiguration";
import ButtonPropertiesJson from "../../../Properties/ButtonPropertiesJson";
import styles from "../../../LeftNavigation/DesignerControls/BasicControls.module.css";
import CollapsibleComponent from "../../../../ReusableComponents/CollapseCard/CollapseCard";
import ColorPicker from "../../../../ReusableComponents/ColorPicker/ColorPicker";
import ToggleBtn from "../../../../ReusableComponents/ToggleBtn/ToggleBtn";
import LinkIcon from "../../../../Icons/LinkIcon";
import SubformIcon from "../../../../Icons/SubformIcon";
import PrimaryButton1 from "../../../../Icons/PrimaryButton1";
import PrimaryButton1A from "../../../../Icons/PrimaryButton1A";
import SecondaryButton from "../../../../Icons/SecondaryButton";
import SecondaryButtonA from "../../../../Icons/SecondaryButtonA";
import TextButton from "../../../../Icons/TextButton";
import TextButtonA from "../../../../Icons/TextButtonA";
import ControlLabel from "../../../../ReusableComponents/ControlLabel/ControlLabel";
import ImageUploadBtn from "../../../../ReusableComponents/ImageUploadBtn/ImageUploadBtn";
import { regexValidator } from "../../../../Resources/globalFunctions";
import CustomizedAlert from "../../../../ReusableComponents/Alert/CustomizedAlert";
import { useTranslation } from "react-i18next";
import { EventListjson } from "../../../../ReusableComponents/Events/EventList";
import Events from "../../../../ReusableComponents/Events/Events";
import ErrorMessageComponent from "../../../../ReusableComponents/ErrorMessageComponent/ErrorMessageComponent";
import { FontWeightListJson } from "../../../Properties/FontWeightList";
import {
  getFormId,
  updateRowNColumnIfExist,
} from "../../../../components/IFormDesignerLayout/IFormDesignerLogic";
import { initializeSubForm } from "../../RequestLogic";
import SubformTemplate from "../../../LeftNavigation/PageTemplate/SubformTemplate";
import { validateCustomControlId } from "../../RequestLogic";
import { updateUserId } from "../../Logic/UserIdLogic";
import JSZip from "jszip";
import axios from "../../../../axios";
import cloneDeep from "lodash-es/cloneDeep";
import BusinessFunction from "../../../../ReusableComponents/BusinessFunctions/BusinessFunctions";
import { GetStateFromData } from "../../../CustomHooks/CustomHooks";

const ButtonProperties = (props) => {
  const contexts = GetStateFromData(props.containerId);

  const {
    updateJson,
    formState2,
    deleteControl,
    setQuickToolsClose,
    currentButtonForSubform,
    setCurrentButtonForSubform,
    showAlertConfirmNotifications,
    setOpenLoader,
    setSelectedButtonEventId,
    setSubFormState,
    setSubformElementIndex,
    recursiveJsonReaderForSubForm,
    setSubformStack,
    setIsButtonSubformOpen,
    setSpareSubForm,
  } = contexts.contexts;
  const { updatedComponentInFormState } = updateJson;
  const translationStyles1 =
    window?.depsatkur_configs?.selectedLanguage === "ar" ||
    window?.depsatkur_configs?.selectedLanguage === "ar-SA";
  const [selectedEventOnClick, setSelectedEventOnClick] = useState(
    props.selectedControl &&
      (props.selectedControl["subform"] &&
      props.selectedControl["subform"] !== null
        ? "subform"
        : props.selectedControl?.style?.["ButtonLink"]
        ? "link"
        : false)
  );

  const [selectedEventOnClick1, setSelectedEventOnClick1] = useState(false);
  const [selectedSubFormEvent, setSelectedSubFormEvent] = useState(false);
  const [errorMessageId, setErrorMessageId] = useState(false);
  const [alertData, setAlertData] = useState({
    open: false,
    showConfirm: "showNotification",
  });
  const { t } = useTranslation(["styles", "translation"]);
  const inputFile = useRef(null);

  function handleEventOnClick(eventName) {
    setSelectedEventOnClick(eventName);
  }

  function handleEventOnClick1(eventName) {
    console.log("handleEventOnClick1 event - ", eventName);
    setSelectedEventOnClick1(eventName);
  }
  const onButtonClick = (evt) => {
    evt.stopPropagation();
    inputFile.current.click();
  };

  function handleSubform(eventName) {
    setQuickToolsClose(true);
    window.setTimeout(() => {
      setQuickToolsClose(false);
    }, 1000);
    window.buttonJson = props;
    setSelectedSubFormEvent(eventName.toLowerCase());
    if (eventName.toLowerCase() === "create") {
      window.currentOpenOverlayParentId = props.selectedControl.id;
      setIsButtonSubformOpen(true);
      var subFormJson = cloneDeep(SubformTemplate);
      subFormJson.pageStyle.SubformTitle = props.selectedControl.id;
      setSpareSubForm(SubformTemplate);
      subFormJson.id = props.selectedControl.id;
      subFormJson.containerControl = props?.selectedControl?.containerControl;

      if (props.selectedControl.subform) {
        subFormJson = props.selectedControl.subform;
      }

      var subForm = props.applyChanges(subFormJson, true);

      setSubformStack((prev) => [...prev, "subform"]);

      setSubformElementIndex(props.selectedControl?.elementIndex);
      var payload = {
        frameIndex: props.selectedControl?.frameIndex,
        elementIndex: props.selectedControl?.elementIndex,
        sheetElementIndex: props.selectedControl?.sheetElementIndex,
        sheetElementFrameIndex: props.selectedControl?.sheetElementFrameIndex,
        sheetFrameElementIndex: props.selectedControl?.sheetFrameElementIndex,
        sheetId: props.selectedControl?.sheetIndex,
        controlid: props.selectedControl?.id,
      };
      setCurrentButtonForSubform(props.selectedControl);
      setSelectedButtonEventId(props.selectedControl.id);
      setSubFormState(subFormJson);

      if (
        props.selectedControl.containerControl === "Menu" ||
        props.selectedControl.containerControl === "wizard_v" ||
        props.selectedControl.containerControl === "wizard_h" ||
        props.selectedControl.containerControl === "sidenavigation"
      ) {
        payload.stepIndex =
          props.selectedControl.stepIndex ||
          props.selectedControl.stepIndex === 0
            ? props.selectedControl.stepIndex
            : "-1";
        payload.subStepIndex =
          props.selectedControl.subStepIndex ||
          props.selectedControl.subStepIndex === 0
            ? props.selectedControl.subStepIndex
            : "-1";
        payload.subChildMenuIndex =
          props.selectedControl.subChildMenuIndex ||
          props.selectedControl.subChildMenuIndex === 0
            ? props.selectedControl.subChildMenuIndex
            : "-1";
      }

      initializeSubForm(payload)
        .then((response) => {
          props.setShowSubForm(true);
        })
        .catch(function (error) {
          console.error("Error :: ", error);
        });
      var updatedParentFormState = recursiveJsonReaderForSubForm(
        formState2,
        props.selectedControl.id,
        subFormJson,
        "subform"
      );
      window.selectedControlId_function(
        props.selectedControl,
        props.selectedControl.id
      );
    } else if (eventName.toLowerCase() === "edit") {
      window.currentOpenOverlayParentId = props.selectedControl.id;
      setIsButtonSubformOpen(true);
      var subFormJson = cloneDeep(props.selectedControl.subform);
      setSpareSubForm(props.selectedControl.subform);
      var subForm = props.applyChanges(subFormJson, true);
      setSubformElementIndex(props.selectedControl?.elementIndex);
      var payload = {
        frameIndex: props.selectedControl?.frameIndex,
        elementIndex: props.selectedControl?.elementIndex,
        sheetElementIndex: props.selectedControl?.sheetElementIndex,
        sheetElementFrameIndex: props.selectedControl?.sheetElementFrameIndex,
        sheetFrameElementIndex: props.selectedControl?.sheetFrameElementIndex,
        sheetId: props.selectedControl?.sheetIndex,
        controlid: props.selectedControl?.id,
      };
      setCurrentButtonForSubform(props.selectedControl);
      setSelectedButtonEventId(props.selectedControl.id);
      setSubFormState(subFormJson);

      if (
        props.selectedControl.containerControl === "Menu" ||
        props.selectedControl.containerControl === "wizard_v" ||
        props.selectedControl.containerControl === "wizard_h" ||
        props.selectedControl.containerControl === "sidenavigation"
      ) {
        payload.stepIndex =
          props.selectedControl.stepIndex ||
          props.selectedControl.stepIndex === 0
            ? props.selectedControl.stepIndex
            : "-1";
        payload.subStepIndex =
          props.selectedControl.subStepIndex ||
          props.selectedControl.subStepIndex === 0
            ? props.selectedControl.subStepIndex
            : "-1";
        payload.subChildMenuIndex =
          props.selectedControl.subChildMenuIndex ||
          props.selectedControl.subChildMenuIndex === 0
            ? props.selectedControl.subChildMenuIndex
            : "-1";
      }

      initializeSubForm(payload)
        .then((response) => {
          props.setShowSubForm(true);
        })
        .catch(function (error) {
          console.error("Error :: ", error);
        });
      window.selectedControlId_function(
        props.selectedControl,
        props.selectedControl.id
      );
    } else {
      window.selectedControlId_function(
        props.selectedControl,
        props.selectedControl.id
      );
    }
  }

  const [jsonToSendForSubform, setJsonToSendForSubform] = useState(undefined);
  const [uploadJsonConfirmation, setUploadJsonConfirmation] = useState(false);
  let jsonToSendForSubformVar;

 

  function handleUpload(evt) {
    const file = evt.target.files[0];
    var subformEmpty = false;
    if (props.selectedControl?.subform) {
      if (Object.keys(props.selectedControl?.subform).length === 0) {
        subformEmpty = true;
      }
    } else {
      subformEmpty = true;
    }
    try {
      if (file.type === "application/x-zip-compressed") {
        JSZip.loadAsync(file).then(function (zip) {
          Object.keys(zip.files).forEach(function (filename) {
            zip.files[filename].async("string").then(function (fileData) {
              var jsonSend = {};
              let jsonObj = JSON.parse(fileData);
              let zip = new JSZip();
              zip.file(filename, JSON.stringify(jsonObj));
              zip.generateAsync({ type: "blob" }).then(function (content) {
                var formDataSub = new FormData();
                formDataSub.append("file", content);
                let formId = getFormId();
                let populateInside = {
                  frameIndex: props.selectedControl.frameIndex,
                  elementIndex: props.selectedControl.elementIndex,
                  sheetElementIndex: props.selectedControl.sheetElementIndex,
                  sheetFrameElementIndex:
                    props.selectedControl.sheetFrameElementIndex,
                  sheetId: props.selectedControl.sheetIndex,
                  stepIndex: props.selectedControl.stepIndex,
                  subStepIndex: props.selectedControl.subStepIndex,
                  subChildMenuIndex: props.selectedControl.subChildMenuIndex,
                  formId: formId.toString(),
                };

                formDataSub.append(
                  "populateInsideJson",
                  JSON.stringify(populateInside)
                );

                axios
                  .post("/idesign/populateSubformFromJsoninFile", formDataSub, {
                    withCredentials: true,
                    headers: {
                      "Content-Type": "multipart/form-data",
                    },
                  })
                  .then((response) => {
                    let data = response.data;
                    let _alert = data.alert;
                    if (_alert) {
                    } else {
                      let populatedJson = data;
                      window.loading(true);
                      if (
                        populatedJson !== null &&
                        populatedJson !== undefined
                      ) {
                        if (response?.data?.formBuffer) {
                          let updatedPopulatedJson =
                            updateRowNColumnIfExist(populatedJson);
                          props.selectedControl["subform"] = JSON.parse(
                            response?.data?.formBuffer
                          );
                          let latestControlJson = {
                            ...props.selectedControl,
                            subform: updatedPopulatedJson,
                          };
                          // console.log("@ latestControlJson ::", latestControlJson);
                          updatedComponentInFormState(
                            props.selectedControl.id,
                            props.selectedControl
                          );
                        }
                      }
                    }
                    showAlertConfirmNotifications({
                      open: true,
                      showConfirm: "showNotification",
                      alertType: "success",
                      alertContentKey: "UploadSubFormSuccessMessage",
                    });

                    window.setTimeout(() => {
                      window.loading(false);
                      showAlertConfirmNotifications({
                        open: false,
                        showConfirm: "showNotification",
                        alertType: "success",
                        alertContentKey: "UploadSubFormSuccessMessage",
                      });
                    }, 1000);
                  })
                  .catch((err) => {
                    setQuickToolsClose((prevState) => {
                      if (prevState === true) {
                        return false;
                      } else {
                        return prevState;
                      }
                    });
                    showAlertConfirmNotifications({
                      open: true,
                      showConfirm: "showNotification",
                      alertType: "error",
                      alertContentKey: "UploadSubFormFailureMessage",
                    });
                    window.setTimeout(() => {
                      showAlertConfirmNotifications({
                        open: false,
                        showConfirm: "showNotification",
                        alertType: "error",
                        alertContentKey: "UploadSubFormFailureMessage",
                      });
                      setOpenLoader(false);
                    }, 1000);
                    console.error("Error :: ", err);
                  });
                setUploadJsonConfirmation(false);
                setJsonToSendForSubform(undefined);
                setOpenLoader(false);
              });
            });
          });
        });
      }
    } catch (error) {
      console.log("@error :: ", error);
    }
    if (!subformEmpty) {
      setAlertData({
        open: true,
        showConfirm: "showconformationDialogBox",
        alertContentKey: "ReplaceConfirmationContent",
        alertTitleKey: "ReplaceSubformConfirmationTitle",
        Button1Key: "CancelButton",
        Button2Key: "ConfirmUpload",
      });
    } else {
      uploadJson(evt);
      setJsonToSendForSubform(undefined);
      jsonToSendForSubformVar = undefined;
    }
    evt.target.files = "";
  }
  function uploadJson(evt) {
    setUploadJsonConfirmation(true);
  }

  function handleCombo(comboId) {
    var select = document.getElementById(comboId);
    var propertyName = select.getAttribute("name");
    var optionTag = select.options[select.selectedIndex];
    handleProperty(propertyName, optionTag.value);
  }

  function validateRegex(propertyName, propertyValue, event, inputBoxId) {
    try {
      let passValue = event?.key;
      if (propertyName === "CustomId") {
        if (event.target.selectionEnd === event.target.selectionStart) {
          passValue = [
            propertyValue.slice(0, event.target.selectionEnd) +
              event.key +
              propertyValue.slice(event.target.selectionEnd),
          ].join("");
        } else {
          let stringArray = propertyValue.split("");
          stringArray.splice(
            event.target.selectionStart,
            event.target.selectionEnd - event.target.selectionStart
          );
          stringArray.splice(
            event.target.selectionStart,
            0,
            ...event?.key.split("")
          );
          passValue = stringArray.join("");
        }
      }
      if (
        event &&
        event.which !== 8 &&
        event.which !== 46 &&
        event.code !== "Tab" &&
        !regexValidator(passValue, ControlsMap[propertyName]["regexType"])
      ) {
        event.preventDefault();
        setAlertData({
          ...ControlsMap["customizedAlertNotificationData"],
          open: true,
          alertContentKey: "RegexErrorMessage",
        });
      } else {
        setAlertData({
          open: false,
          showConfirm: "showNotification",
        });
        setErrorMessageId(false);
      }
    } catch (e) {
      console.log("Error while validating regex");
    }
  }

  function handleProperty(propertyName, propertyValue, event) {
    if (propertyName === "CustomId") {
      if (propertyValue !== props.selectedControl.style.CustomId) {
        var element = event.target;
        var jsonToSend = {
          customControlId: propertyValue,
          controlid: props.selectedControl.id,
        };
        validateCustomControlId(jsonToSend)
          .then((response) => {
            if (response && response.data && response.data.status === "0") {
              let temp_style = props.selectedControl.style;
              let updatedJson = updateUserId(
                props.selectedControl, // whole control Json
                propertyValue //newly defined id
              );
              if (updatedJson) {
                updatedComponentInFormState(
                  props.selectedControl.id,
                  updatedJson
                );
              }
            } else if (
              response &&
              response.data &&
              response.data.status === "-1"
            ) {
              showAlertConfirmNotifications({
                open: true,
                showConfirm: "showNotification",
                alertType: "error",
                alertContentKey: "CustomControl Id already Exist",
              });

              element.value = props.selectedControl.style.CustomId || "";
            }
          })
          .catch(function (error) {
            element.value = props.selectedControl.style.CustomId || "";
            console.log(error);
            showAlertConfirmNotifications({
              open: true,
              showConfirm: "showNotification",
              alertType: "error",
              alertContentKey: `Server side error ${error}`,
            });
          });
      }
    } else {
      let temp_style = props.selectedControl.style;
      temp_style[propertyName] = propertyValue;
      props.selectedControl.style[propertyName] = propertyValue;

      if (propertyValue === "Regular Italic") {
        props.selectedControl.style[propertyName] = "300";
        props.selectedControl.style["FontStyle"] = "italic";
        props.selectedControl.style["WeightStyle"] = propertyValue;
      } else if (propertyValue === "Semibold") {
        props.selectedControl.style[propertyName] = "600";
        props.selectedControl.style["FontStyle"] = "normal";
        props.selectedControl.style["WeightStyle"] = propertyValue;
      } else if (propertyValue === "Bold") {
        props.selectedControl.style[propertyName] = "bold";
        props.selectedControl.style["FontStyle"] = "normal";
        props.selectedControl.style["WeightStyle"] = propertyValue;
      } else if (propertyValue === "Regular") {
        props.selectedControl.style[propertyName] = "300";
        props.selectedControl.style["FontStyle"] = "normal";
        props.selectedControl.style["WeightStyle"] = propertyValue;
      } else if (propertyValue === "Bold Italic") {
        props.selectedControl.style[propertyName] = "bold";
        props.selectedControl.style["FontStyle"] = "italic";
        props.selectedControl.style["WeightStyle"] = propertyValue;
      } else if (propertyValue === "Light Italic") {
        props.selectedControl.style[propertyName] = "100";
        props.selectedControl.style["FontStyle"] = "italic";
        props.selectedControl.style["WeightStyle"] = propertyValue;
      } else if (propertyValue === "Semibold Italic") {
        props.selectedControl.style[propertyName] = "600";
        props.selectedControl.style["FontStyle"] = "italic";
        props.selectedControl.style["WeightStyle"] = propertyValue;
      } else if (propertyValue === "Light") {
        props.selectedControl.style[propertyName] = "100";
        props.selectedControl.style["FontStyle"] = "normal";
        props.selectedControl.style["WeightStyle"] = propertyValue;
      } else if (propertyValue === "") {
        props.selectedControl.style[propertyName] = "";
        props.selectedControl.style["FontStyle"] = "normal";
        props.selectedControl.style["WeightStyle"] = propertyValue;
      }

      updatedComponentInFormState(
        props.selectedControl.id,
        props.selectedControl
      );
    }
  }

  function selectedOptionForButtonType(value) {
    let temp_style = props.selectedControl.style;
    temp_style["ButtonType"] = value;
    props.selectedControl.style["ButtonType"] = value;
    updatedComponentInFormState(
      props.selectedControl.id,
      props.selectedControl
    );
  }

  var CollapsibleItems = [];
  Object.keys(ButtonPropertiesJson).forEach((key, index) => {
    var collapseComponentBody = [];
    if (
      key === "Button Events" &&
      currentButtonForSubform !== undefined &&
      currentButtonForSubform.id !== props.selectedControl.id
    ) {
    } else {
      Object.keys(ButtonPropertiesJson[key]).forEach((property) => {
        if (key === "Button Style") {
          if (
            props.selectedControl.style !== undefined &&
            (props.selectedControl.style["ButtonType"] === "Secondary" ||
              (props?.formData?.theme?.ButtonStyle?.ControlStyle ===
                "Secondary" &&
                !props.selectedControl.style["ButtonType"]))
          ) {
            if (property !== "BackgroundColor") {
              if (
                ControlsMap[property] !== undefined &&
                ControlsMap[property] !== null
              ) {
                collapseComponentBody.push(
                  prepareHtmlTag(property, ControlsMap[property].type)
                );
              }
            }
          } else if (
            props.selectedControl.style !== undefined &&
            (props.selectedControl.style["ButtonType"] === "Text" ||
              (props?.formData?.theme?.ButtonStyle?.ControlStyle === "Text" &&
                !props.selectedControl.style["ButtonType"]))
          ) {
            if (
              property !== "BorderColor" &&
              property !== "BackgroundColor" &&
              property !== "CornerRadius" &&
              property !== "BorderWidth"
            ) {
              if (
                ControlsMap[property] !== undefined &&
                ControlsMap[property] !== null
              ) {
                collapseComponentBody.push(
                  prepareHtmlTag(property, ControlsMap[property].type)
                );
              }
            }
          } else {
            if (property !== "BorderColor" && property !== "BorderWidth") {
              if (
                ControlsMap[property] !== undefined &&
                ControlsMap[property] !== null
              ) {
                collapseComponentBody.push(
                  prepareHtmlTag(property, ControlsMap[property].type)
                );
              } else {
                // console.log('WONT_WORK_FOR::'+property);
              }
            }
          }
        } else {
          if (
            ControlsMap[property] !== null &&
            ControlsMap[property] !== undefined
          ) {
            collapseComponentBody.push(
              prepareHtmlTag(property, ControlsMap[property].type)
            );
          }
        }
      });
    }
    if (CollapsibleItems === []) {
      if (
        key === "Button Events" &&
        currentButtonForSubform !== undefined &&
        currentButtonForSubform.id !== props.selectedControl.id
      ) {
      } else {
        CollapsibleItems.push(
          <CollapsibleComponent
            id={index + ""}
            heading={key}
            body={collapseComponentBody}
          />
        );
      }
    }

    if (
      key === "Button Events" &&
      currentButtonForSubform !== undefined &&
      currentButtonForSubform.id !== props.selectedControl.id
    ) {
    } else {
      CollapsibleItems.push(
        <CollapsibleComponent
          id={index + ""}
          heading={key}
          body={collapseComponentBody}
        />
      );
    }
  });

  function handleToggle(e, field) {
    const value = e.target.value;
    props.selectedControl.style[field] = value;
    if (field === "Enable") {
      props.selectedControl.style["previousEnable"] = value;
    }
    if (field === "ReadOnlyStyle") {
      props.selectedControl.style["previousReadOnly"] = value;
    }
    if (field === "ReadOnly") {
      props.selectedControl.style["previousReadOnlyValue"] = value;
    }
    updatedComponentInFormState(
      props.selectedControl.id,
      props.selectedControl
    );
  }

  const style = props.selectedControl.style;
  function handleIcon(iconName, iconFile) {
    const temp_style = style;
    temp_style["icon"] = { name: "", file: "" };
    temp_style["icon"]["name"] = iconName;
    temp_style["icon"]["file"] = iconFile;
    props.selectedControl.style["iconName"] = iconName;
    props.selectedControl.style["iconFile"] = iconFile;
    updatedComponentInFormState(
      props.selectedControl.id,
      props.selectedControl
    );
  }

  const updateEventData = (eventJson) => {
    var count = Object.keys(eventJson).length;
    if (count > 0) {
      var Events = {};
      Events["Events"] = eventJson;
      props.selectedControl["event"] = Events;
    }
    updatedComponentInFormState(
      props.selectedControl.id,
      props.selectedControl
    );
  };

  const updateBusinessFunctionData = (businessJson) => {
    var BusinessFunctions = {};
    BusinessFunctions["BusinessFunctions"] = businessJson;
    props.selectedControl["businessfunction"] = BusinessFunctions;

    updatedComponentInFormState(
      props.selectedControl.id,
      props.selectedControl
    );
  };

  function prepareHtmlTag(key, type) {
    switch (type.toLowerCase()) {
      case "combo":
        if (key === "ButtonType") {
          return (
            <div
              className={styles.parentDiv}
              style={translationStyles1 ? { display: "flex" } : null}
            >
              <span className={styles.leftLabel}>
                {t(`translation:${ControlsMap[key].label}`)}
              </span>
              <div className={styles.RightLabelDiv}>
                <select
                  id={props.selectedControl.id + ControlsMap[key].label}
                  name={key}
                  className={styles.RightLabelSelect}
                  onChange={() =>
                    handleCombo(
                      props.selectedControl.id + ControlsMap[key].label
                    )
                  }
                  key={props.selectedControl.id}
                >
                  {ControlsMap[key].children.map(
                    (optionTagData, optionCount) => {
                      if (ControlsMap[key].children[optionCount].Value) {
                        return (
                          <option
                            style={
                              ControlsMap[key].children[optionCount]?.style
                            }
                            value={ControlsMap[key].children[optionCount].Value}
                            selected
                          >
                            {t(
                              `translation:${ControlsMap[key].children[optionCount].Title}`
                            )}
                          </option>
                        );
                      } else {
                        return (
                          <option
                            style={
                              ControlsMap[key].children[optionCount]?.style
                            }
                            value={ControlsMap[key].children[optionCount].Value}
                          ></option>
                        );
                      }
                    }
                  )}
                </select>
              </div>
            </div>
          );
        } else if (key === "ButtonAlignment") {
          return (
            <div
              className={styles.parentDiv}
              style={translationStyles1 ? { display: "flex" } : null}
            >
              <div className={styles.buttonIconAlignment}>
                <span className={styles.leftLabel}>
                  {t(`translation:${ControlsMap[key].label}`)}
                </span>
                <div className={styles.RightLabelSelectDiv1}>
                  <select
                    className={styles.RightLabelSelectButtonCombo}
                    name={key}
                    id={props.selectedControl.id + ControlsMap[key].label}
                    onChange={() =>
                      handleCombo(
                        props.selectedControl.id + ControlsMap[key].label
                      )
                    }
                    key={props.selectedControl.id}
                  >
                    {ControlsMap[key].children.map(
                      (optionTagData, optionCount) => {
                        if (ControlsMap[key].children[optionCount].Value) {
                          return (
                            <>
                              {ControlsMap[key].children[optionCount].Value ===
                              props.selectedControl.style[key] ? (
                                <option
                                  value={
                                    ControlsMap[key].children[optionCount].Value
                                  }
                                  selected
                                >
                                  {t(
                                    `translation:${ControlsMap[key].children[optionCount].Title}`
                                  )}
                                </option>
                              ) : (
                                <option
                                  value={
                                    ControlsMap[key].children[optionCount].Value
                                  }
                                >
                                  {t(
                                    `translation:${ControlsMap[key].children[optionCount].Title}`
                                  )}
                                </option>
                              )}
                            </>
                          );
                        }
                      }
                    )}
                  </select>
                </div>
              </div>
            </div>
          );
        } else {
          return (
            <div
              className={styles.parentDiv}
              style={translationStyles1 ? { display: "flex" } : null}
            >
              <span className={styles.leftLabel}>
                {t(`translation:${ControlsMap[key].label}`)}
              </span>
              <div className={styles.RightLabelSelectDiv2}>
                {key !== "FontWeight" ? (
                  <>
                    <select
                      className={styles.RightLabelSelect1}
                      name={key}
                      id={props.selectedControl.id + key}
                      onChange={() =>
                        handleCombo(props.selectedControl.id + key)
                      }
                      key={props.selectedControl.id}
                    >
                      {ControlsMap[key].children.map(
                        (optionTagData, optionCount) => {
                          if (ControlsMap[key].children[optionCount].Title) {
                            return (
                              <>
                                {ControlsMap[key].children[optionCount]
                                  .Value ===
                                props.selectedControl.style[key] ? (
                                  <option
                                    style={
                                      ControlsMap[key].children[optionCount]
                                        ?.style
                                    }
                                    value={
                                      ControlsMap[key].children[optionCount]
                                        .Value
                                    }
                                    selected
                                  >
                                    {t(
                                      `translation:${ControlsMap[key].children[optionCount].Title}`
                                    )}
                                  </option>
                                ) : (
                                  <option
                                    style={
                                      ControlsMap[key].children[optionCount]
                                        ?.style
                                    }
                                    value={
                                      ControlsMap[key].children[optionCount]
                                        .Value
                                    }
                                  >
                                    {t(
                                      `translation:${ControlsMap[key].children[optionCount].Title}`
                                    )}
                                  </option>
                                )}
                              </>
                            );
                          }
                        }
                      )}
                    </select>
                  </>
                ) : (
                  <>
                    <select
                      id={props.selectedControl.id + key}
                      className={styles.RightLabelSelect1}
                      name={key}
                      onChange={() =>
                        handleCombo(props.selectedControl.id + key)
                      }
                      key={props.selectedControl.id}
                    >
                      {props.selectedControl.style &&
                      props.selectedControl.style["FontFamily"] ? (
                        <>
                          {FontWeightListJson[
                            props.selectedControl.style["FontFamily"]
                          ]?.children.map((optionTagData, optionCount) => {
                            if (
                              FontWeightListJson[
                                props.selectedControl.style["FontFamily"]
                              ].children[optionCount].Title
                            ) {
                              return (
                                <>
                                  {FontWeightListJson[
                                    props.selectedControl.style["FontFamily"]
                                  ].children[optionCount].Value ===
                                  props?.selectedControl?.style[
                                    "WeightStyle"
                                  ] ? (
                                    <option
                                      value={
                                        FontWeightListJson[
                                          props.selectedControl.style[
                                            "FontFamily"
                                          ]
                                        ].children[optionCount].Value
                                      }
                                      selected
                                    >
                                      {
                                        FontWeightListJson[
                                          props.selectedControl.style[
                                            "FontFamily"
                                          ]
                                        ].children[optionCount].Title
                                      }
                                    </option>
                                  ) : (
                                    <option
                                      value={
                                        FontWeightListJson[
                                          props.selectedControl.style[
                                            "FontFamily"
                                          ]
                                        ].children[optionCount].Value
                                      }
                                    >
                                      {
                                        FontWeightListJson[
                                          props.selectedControl.style[
                                            "FontFamily"
                                          ]
                                        ].children[optionCount].Title
                                      }
                                    </option>
                                  )}
                                </>
                              );
                            }
                          })}
                        </>
                      ) : (
                        <>
                          {FontWeightListJson["Open Sans"].children.map(
                            (optionTagData, optionCount) => {
                              if (
                                FontWeightListJson["Open Sans"].children[
                                  optionCount
                                ].Title
                              ) {
                                return (
                                  <>
                                    {FontWeightListJson["Open Sans"].children[
                                      optionCount
                                    ].Value ===
                                    props.selectedControl.style[
                                      "WeightStyle"
                                    ] ? (
                                      <option
                                        value={
                                          FontWeightListJson["Open Sans"]
                                            .children[optionCount].Value
                                        }
                                        selected
                                      >
                                        {t(
                                          `translation:${FontWeightListJson["Open Sans"].children[optionCount].Title}`
                                        )}
                                      </option>
                                    ) : (
                                      <option
                                        value={
                                          FontWeightListJson["Open Sans"]
                                            .children[optionCount].Value
                                        }
                                      >
                                        {t(
                                          `translation:${FontWeightListJson["Open Sans"].children[optionCount].Title}`
                                        )}
                                      </option>
                                    )}
                                  </>
                                );
                              }
                            }
                          )}
                        </>
                      )}
                    </select>
                  </>
                )}
              </div>
            </div>
          );
        }

      case "text":
        return (
          <div className={styles.parentDiv} style={{ display: "block" }}>
            <span className={styles.leftLabel}>
              {t(`translation:${ControlsMap[key].label}`)}
            </span>
            {key !== "CustomControlID" ? (
              <div
                className={
                  styles.RightLabelDivButtonLabel + " " + styles.btnUserId
                }
              >
                <input
                  aria-label={"Control Value1"}
                  type="text"
                  className={[
                    styles.RightLabelTextInput,
                    errorMessageId === props.selectedControl.id + key
                      ? styles.inputBoxErrorStyle
                      : null,
                  ].join(" ")}
                  style={{ width: "100%" }}
                  defaultValue={props.selectedControl.style["Label"]}
                  onKeyPress={(event) =>
                    validateRegex(
                      key,
                      event.target.value,
                      event,
                      props.selectedControl.id + key
                    )
                  }
                  onKeyUp={(event) =>
                    validateRegex(
                      key,
                      event.target.value,
                      event,
                      props.selectedControl.id + key
                    )
                  }
                  onBlur={(event) => handleProperty(key, event.target.value)}
                  key={props.selectedControl.id}
                />
              </div>
            ) : (
              <>
                <div className={styles.RightLabelDivCustomID}>
                  <input
                    aria-label={"Control Value2"}
                    type="text"
                    className={[
                      styles.RightLabelCustomID,
                      errorMessageId === props.selectedControl.id + key
                        ? styles.inputBoxErrorStyle
                        : null,
                    ].join(" ")}
                    onKeyPress={(event) =>
                      validateRegex(
                        key,
                        event.target.value,
                        event,
                        props.selectedControl.id + key
                      )
                    }
                    onKeyUp={(event) =>
                      validateRegex(
                        key,
                        event.target.value,
                        event,
                        props.selectedControl.id + key
                      )
                    }
                    onBlur={(event) => handleProperty(key, event.target.value)}
                  />
                </div>
              </>
            )}
            {errorMessageId === props.selectedControl.id + key ? (
              <ErrorMessageComponent errorMessage="non-permissible value" />
            ) : null}
          </div>
        );

      case "colorpicker":
        return (
          <div className={styles.buttonColorpicker}>
            {t(`translation:${ControlsMap[key].label}`)}
            <ColorPicker
              key={key + props.selectedControl.id}
              color={props.selectedControl.style[key]}
              handleProperty={handleProperty}
              propertyName={key}
            />
          </div>
        );
      case "toggle":
        return (
          <div
            className={styles.parentDiv}
            style={translationStyles1 ? { display: "flex" } : null}
          >
            <span className={styles.leftLabel}>
              {t(`translation:${ControlsMap[key].label}`)}
            </span>
            <div
              className={
                translationStyles1
                  ? styles.toggleCheckboxArabic
                  : styles.toggleCheckbox
              }
            >
              <ToggleBtn
                key={props.selectedControl.id}
                toggleState={props.selectedControl.style[key]}
                handleToggle={handleToggle}
                field={key}
                id={
                  props?.selectedControl?.style?.CustomId
                    ? props?.selectedControl?.style?.CustomId
                    : props?.selectedControl?.id
                }
              />
            </div>
          </div>
        );

      case "text_top_bottom":
        return (
          <Form.Group className={styles.parentDivTopBottom}>
            <div>
              <span className={styles.topBottomLabel}>
                {t(`translation:${ControlsMap[key].label}`)}
              </span>
            </div>
            <div>
              <input
                aria-label={"Control Value3"}
                type="text"
                defaultValue={props.selectedControl.style[key]}
                key={props.selectedControl.id}
                id={props.selectedControl.id + key}
                className={[
                  styles.fullWidthTextBox,
                  errorMessageId === props.selectedControl.id + key
                    ? styles.inputBoxErrorStyle
                    : null,
                ].join(" ")}
                onKeyPress={(event) =>
                  validateRegex(
                    key,
                    event.target.value,
                    event,
                    props.selectedControl.id + key
                  )
                }
                onKeyUp={(event) =>
                  validateRegex(
                    key,
                    event.target.value,
                    event,
                    props.selectedControl.id + key
                  )
                }
                onBlur={(event) =>
                  handleProperty(key, event.target.value, event)
                }
              />
            </div>

            {errorMessageId === props.selectedControl.id + key ? (
              <ErrorMessageComponent errorMessage="non-permissible value" />
            ) : null}
          </Form.Group>
        );

      case "textbox":
        return (
          <div>
            <div className={styles.leftLabelForTextBox}>
              <div className={styles.labelForLabel}>
                {t(`translation:${ControlsMap[key].label}`)}{" "}
              </div>
              <input
                aria-label={"Control Value4"}
                type="text"
                defaultValue={props.selectedControl.style[key]}
                key={
                  props.selectedControl.id + props.selectedControl.style[key]
                }
                id={props.selectedControl.id + key}
                className={[
                  styles.textBoxLayoutForLable,
                  errorMessageId === props.selectedControl.id + key
                    ? styles.inputBoxErrorStyle
                    : null,
                ].join(" ")}
                onKeyPress={(event) =>
                  validateRegex(
                    key,
                    event.target.value,
                    event,
                    props.selectedControl.id + key
                  )
                }
                onKeyUp={(event) =>
                  validateRegex(
                    key,
                    event.target.value,
                    event,
                    props.selectedControl.id + key
                  )
                }
                onBlur={(event) =>
                  handleProperty(key, event.target.value, event)
                }
                maxLength={window?.depsatkur_configs?.label_max_length}
              />
            </div>
            {errorMessageId === props.selectedControl.id + key ? (
              <ErrorMessageComponent errorMessage="non-permissible value" />
            ) : null}
          </div>
        );

      case "textarea":
        return (
          <>
            {ControlsMap[key].label !== "Tooltip" ? (
              <div className={styles.textAreaParentDiv}>
                <span className={styles.leftLabelForButton}>
                  {t(`translation:${ControlsMap[key].label}`)}
                </span>
                <div>
                  <textarea
                    aria-label={props.selectedControl.style[key]}
                    onKeyPress={(event) =>
                      validateRegex(
                        key,
                        event.target.value,
                        event,
                        props.selectedControl.id + key
                      )
                    }
                    onKeyUp={(event) =>
                      validateRegex(
                        key,
                        event.target.value,
                        event,
                        props.selectedControl.id + key
                      )
                    }
                    onBlur={(event) => handleProperty(key, event.target.value)}
                    defaultValue={props.selectedControl.style[key]}
                    key={props.selectedControl.id}
                    className={[
                      styles.textAreaDivForButton,
                      errorMessageId === props.selectedControl.id + key
                        ? styles.inputBoxErrorStyle
                        : null,
                    ].join(" ")}
                  />
                </div>
                {errorMessageId === props.selectedControl.id + key ? (
                  <ErrorMessageComponent errorMessage="non-permissible value" />
                ) : null}
              </div>
            ) : null}
          </>
        );

      case "fullwidthtextinput":
        return (
          <div className={styles.textAreaParentDiv}>
            <span className={styles.leftLabelForButton}>
              {t(`translation:${ControlsMap[key].label}`)}
            </span>
            <div>
              <input
                aria-label={"Control Value5"}
                type="text"
                className={[
                  styles.fullWidthInputButton,
                  errorMessageId === props.selectedControl.id + key
                    ? styles.inputBoxErrorStyle
                    : null,
                ].join(" ")}
                onKeyPress={(event) =>
                  validateRegex(
                    key,
                    event.target.value,
                    event,
                    props.selectedControl.id + key
                  )
                }
                onKeyUp={(event) =>
                  validateRegex(
                    key,
                    event.target.value,
                    event,
                    props.selectedControl.id + key
                  )
                }
                key={props.selectedControl.id}
                onBlur={(event) => handleProperty(key, event.target.value)}
              />
            </div>
            {errorMessageId === props.selectedControl.id + key ? (
              <ErrorMessageComponent errorMessage="non-permissible value" />
            ) : null}
          </div>
        );

      case "boxwithicon":
        return (
          <div className={styles.butonsEventsParentDivOnClick}>
            <span className={styles.leftLabel}>
              {t(`translation:${ControlsMap[key].label}`)}
            </span>
            <br />
            <div className={styles.eventsOnClick}>
              {ControlsMap[key].children.map((optionTagData, optionCount) => {
                switch (
                  ControlsMap[key].children[optionCount].Title.toLowerCase()
                ) {
                  case "subform":
                    return (
                      <div
                        className={
                          selectedEventOnClick === "subform"
                            ? styles.eventsOnClickSelectedInnerDiv
                            : styles.eventsOnClickInnerDiv
                        }
                        id={ControlsMap[key].children[
                          optionCount
                        ].Title.toLowerCase()}
                        onClick={() =>
                          handleEventOnClick(
                            ControlsMap[key].children[
                              optionCount
                            ].Title.toLowerCase()
                          )
                        }
                      >
                        <SubformIcon />
                      </div>
                    );
                  case "link":
                    return (
                      <div
                        className={
                          selectedEventOnClick === "link"
                            ? styles.eventsOnClickSelectedInnerDiv
                            : styles.eventsOnClickInnerDiv
                        }
                        id={ControlsMap[key].children[
                          optionCount
                        ].Title.toLowerCase()}
                        onClick={() =>
                          handleEventOnClick(
                            ControlsMap[key].children[
                              optionCount
                            ].Title.toLowerCase()
                          )
                        }
                      >
                        <LinkIcon
                          style={
                            props.selectedControl?.style?.ButtonLink
                              ? { stroke: "#0072c6" }
                              : {}
                          }
                        />
                      </div>
                    );
                }
              })}
            </div>
            <div className={styles.eventsOnClickTextParentDiv}>
              {ControlsMap[key].children.map((optionTagData, optionCount) => {
                return (
                  <>
                    {ControlsMap[key].children[optionCount].Title !== "Page" &&
                    ControlsMap[key].children[optionCount].Title !== "URL" &&
                    ControlsMap[key].children[optionCount].Title !== "Code" &&
                    ControlsMap[key].children[optionCount].Title !== "More" ? (
                      <div className={styles.eventsOnClickTextInnerDiv}>
                        {t(
                          `translation:${ControlsMap[key].children[optionCount].Title}`
                        )}
                      </div>
                    ) : null}
                  </>
                );
              })}
            </div>
          </div>
        );

      case "subformbox":
        return (
          <>
            {selectedEventOnClick === "subform" ? (
              <>
                <div className={styles.butonsEventsParentDiv}>
                  <span className={styles.leftLabel}>
                    {t(`translation:${ControlsMap[key].label}`)}
                  </span>
                  <br />
                  <div className={styles.subForm}>
                    {ControlsMap[key].children.map(
                      (optionTagData, optionCount) => {
                        var subform =
                          props.selectedControl.subform !== undefined
                            ? "Edit"
                            : "Create";
                        return (
                          <>
                            {ControlsMap[key].children[optionCount].Title !==
                            "Upload" ? (
                              <>
                                <div
                                  className={styles.eventsOnClickInnerDiv}
                                  onClick={() => {
                                    handleEventOnClick1(
                                      ControlsMap[key].children[
                                        optionCount
                                      ].Title.toLowerCase()
                                    );
                                    handleSubform(subform);
                                  }}
                                >
                                  {props.selectedControl &&
                                  props.selectedControl["subform"] &&
                                  props.selectedControl["subform"] !== null
                                    ? t("translation:Edit")
                                    : t(
                                        `translation:${ControlsMap[key].children[optionCount].Title}`
                                      )}
                                </div>
                              </>
                            ) : (
                              <>
                                {selectedEventOnClick1 == "create" ? null : (
                                  <>
                                    <div
                                      className={
                                        selectedSubFormEvent &&
                                        ControlsMap[key].children[optionCount]
                                          .Title === subform
                                          ? styles.eventsOnClickTextSelectedInnerDiv
                                          : styles.eventsOnClickInnerDiv
                                      }
                                      onClick={onButtonClick}
                                    >
                                      {t(
                                        `translation:${ControlsMap[key].children[optionCount].Title}`
                                      )}
                                    </div>
                                    <input
                                      aria-label={"Control Value6"}
                                      type="file"
                                      id="file"
                                      ref={inputFile}
                                      style={{ display: "none" }}
                                      onChange={(e) => handleUpload(e)}
                                      accept="application/zip"
                                    />
                                  </>
                                )}
                              </>
                            )}
                          </>
                        );
                      }
                    )}
                  </div>
                </div>
                <>
                  {selectedSubFormEvent === "select" ? (
                    <div className={styles.parentDiv}>
                      <span className={styles.leftLabel}>
                        {t(`translation:${"Select Subform"}`)}
                      </span>
                      <div className={styles.RightLabelDivSelectSubform}>
                        <select className={styles.RightLabelSelectComboSubform}>
                          <option>{t(`translation:${"Select"}`)}</option>
                        </select>
                      </div>
                    </div>
                  ) : null}
                </>
              </>
            ) : null}
          </>
        );
      case "longinput":
        return (
          <>
            {key === "ButtonLink" && selectedEventOnClick === "link" ? (
              <div className="longinput">
                {t(`translation:${ControlsMap[key].label}`)}
                <input
                  aria-label={"Control Value7"}
                  className="longinput_input"
                  style={{ height: "24px" }}
                  autoComplete="off"
                  rows="2"
                  defaultValue={
                    key === "label"
                      ? props.selectedControl[key]
                      : props.selectedControl.style[key]
                  }
                  key={props.selectedControl.id}
                  onBlur={(e) => handleProperty(key, e.target.value)}
                />
              </div>
            ) : null}
          </>
        );

      case "fileupload":
        return (
          <div
            className={styles.parentDiv}
            style={translationStyles1 ? { display: "flex" } : null}
          >
            <span
              className={styles.leftLabelButtonIcon}
              style={{ "margin-top": "0px" }}
            >
              {t(`translation:${ControlsMap[key].label}`)}
            </span>
            <div className={styles.RightLabelDivFileUpload}>
              <div
                className={styles.ButtonIconUploadBtn}
                style={{ marginBottom: "-9px" }}
              >
                <ImageUploadBtn
                  containerId={props.containerId}
                  handleImageUpload={handleIcon}
                  iconName={props.selectedControl.style.iconName}
                />
              </div>
            </div>
          </div>
        );

      case "icon":
        return (
          <>
            <span className={styles.leftLabelButtonType}>
              {t(`translation:${ControlsMap[key].label}`)}
            </span>
            <div
              className={styles.buttonType}
              style={translationStyles1 ? { marginRight: "15px" } : null}
            >
              <Row>
                <div
                  className={
                    props.selectedControl.style["ButtonType"] === "Primary"
                      ? styles.buttonLayoutStyle2
                      : styles.buttonLayoutStyle
                  }
                >
                  <div
                    className={styles.StyleForButton}
                    style={{ direction: "ltr" }}
                    onClick={() => selectedOptionForButtonType("Primary")}
                  >
                    {window?.depsatkur_configs?.selectedLanguage ===
                      "ar" ||
                    window?.depsatkur_configs?.selectedLanguage ===
                      "ar-SA" ? (
                      <PrimaryButton1A />
                    ) : (
                      <PrimaryButton1 />
                    )}
                  </div>
                </div>

                <div
                  className={
                    props.selectedControl.style["ButtonType"] === "Secondary"
                      ? styles.buttonLayoutStyle2
                      : styles.buttonLayoutStyle
                  }
                >
                  <div
                    className={styles.StyleForButton}
                    style={{ direction: "ltr" }}
                    onClick={() => selectedOptionForButtonType("Secondary")}
                  >
                    {window?.depsatkur_configs?.selectedLanguage ===
                      "ar" ||
                    window?.depsatkur_configs?.selectedLanguage ===
                      "ar-SA" ? (
                      <SecondaryButtonA />
                    ) : (
                      <SecondaryButton />
                    )}
                  </div>
                </div>

                <div
                  className={
                    props.selectedControl.style["ButtonType"] === "Text"
                      ? styles.buttonLayoutStyle2
                      : styles.buttonLayoutStyle
                  }
                >
                  <div
                    className={styles.StyleForButton}
                    style={{ direction: "ltr" }}
                    onClick={() => selectedOptionForButtonType("Text")}
                  >
                    {window?.depsatkur_configs?.selectedLanguage ===
                      "ar" ||
                    window?.depsatkur_configs?.selectedLanguage ===
                      "ar-SA" ? (
                      <TextButtonA />
                    ) : (
                      <TextButton />
                    )}
                  </div>
                </div>
              </Row>
            </div>
          </>
        );
      case "eventbutton":
        return (
          <Events
            key={props.selectedControl.id + "_eventsProperty"}
            json={EventListjson}
            value={props.selectedControl.event}
            elemtype={props.selectedControl.elem_type}
            updateEventData={(eventJson) => updateEventData(eventJson)}
          ></Events>
        );
      case "businessfunctionbutton":
        return (
          <BusinessFunction
            updateBusinessFunctionData={(businessJson) =>
              updateBusinessFunctionData(businessJson)
            }
            value={props.selectedControl}
            microFrontendData={props.microFrontendData}
            formState2={formState2}
          />
        );
      default:
        return null;
    }
  }

  return (
    <>
      <ControlLabel
        label="Button"
        handleDelete={deleteControl}
        id={props.selectedControl.id}
        customId={props?.selectedControl?.style?.CustomId}
      />
      <div className="propertiesBody">
        <Card.Body className={styles.cardBodyButtonProperties}>
          {CollapsibleItems.map((item, index) => {
            // console.log("@CollapsibleItem :: ", item);
            // if(ite)
            return item;
          })}
        </Card.Body>
      </div>
      {jsonToSendForSubform !== undefined && alertData?.open && (
        <CustomizedAlert
          alertData={alertData}
          handleEventFunction={(evt) => uploadJson(evt)}
        />
      )}
    </>
  );
};

export default ButtonProperties;
