import { useState, useEffect } from "react";
import BusinessFunctionBackdrop from "./BusinessFunctionBackdrop";
import styles from "./Functions.module.css";
import axios from "../../axios";
import { CheckCircle, DeleteOutline } from "@material-ui/icons";
import { useTranslation } from "react-i18next";
import DeleteConfirmation from "./Components/DeleteConfirmation";
import { makeStyles } from "@material-ui/core";
import CustomizedAlert from "../Alert/CustomizedAlert";
import {ReactComponent as Edit_Function} from "../../Icons/Edit_Function.svg";
import {ReactComponent as Download_Function} from "../../Icons/Download_Function.svg"
import {ReactComponent as Copy_Function} from "../../Icons/Copy_Function.svg"
import deLink from "../../Icons/deLink.svg"
import { getFormId } from "../../components/IFormDesignerLayout/IFormDesignerLogic";
import ShowDataList from "./Components/ShowDataList";
import LinkIcon from "../../Icons/LinkIcon";
import secureLocalStorage from "react-secure-storage";
import { GetStateFromData } from "../../components/CustomHooks/CustomHooks";

const BusinessFunction = (props) => {
  let prevValue = props?.value?.businessfunction?.BusinessFunctions || [];
  const [functionDialogOpen, setFunctionDialogOpen] = useState(false);
  const [deleteDialogOpen, setDeleteDialogOpen] = useState(false);
  const [dataListOpen, setDataListOpen] = useState(false)
  const [dataList, setDataList] = useState([])
  const [deleteId, setDeleteId] = useState(null);
  const [updated, setUpdated] = useState(false);
  const [deleteName, setDeleteName] = useState("");
  const [isAllFilled, setIsAllFilled] = useState(false);
  const [isAssociate, setIsAssociate] = useState(false);
  const [isAssociated, setIsAssociated] = useState(false)
  const [associateData, setAssociateData] = useState([]);
  const [draftData, setDraftData] = useState([])
  const [defaultValue, setDefaultValue] = prevValue;
  const [availableFunction, setAvailableFunction] = useState([]);
  const [publishedFunction, setPublishedFunction] = useState([]);
  const [draftFunction, setDraftFunction] = useState([])
  const [updateData, setUpdateData] = useState(false)
  const [alertData, setAlertData] = useState(false)
  const contexts = GetStateFromData(props.containerId);

  const { showAlertConfirmNotifications } = contexts.contexts;

  const { t } = useTranslation(["styles", "translation"]);

  const getAllData = async () => {
    window.loading(true)
    await axios
      .get(`customRestApi/getRestEndPoints/${props?.microFrontendData?.formDefId ? "P" : "A"}`, {
        withCredentials: true,
      })
      .then((res) => {
        setAvailableFunction(res.data);
        setTimeout(() => {
          if(prevValue && prevValue[0]?.name){
            document.getElementById(prevValue[0]?.name)?.scrollIntoView({ behavior: "smooth", block: "nearest", inline: "nearest" })
          }
        }, 2000)
        window.loading(false)
      })
      .catch((err) => {
        setAlertData({
          'open':true,
          "alertContentMsg": `${t(`translation:${err.response}`)}`,
          'showConfirm':'showNotification',
          'alertType':'error',
          "autoHideDuration": 4000,
      })
      window.loading(false)
      });
  };

  const deleteData = async (id) => {
    window.loading(true)
    await axios
      .get(`customRestApi/deleteRestEndPoint/${id}/`, {
        withCredentials: true,
      })
      .then((res) => {
        window.loading(false)
        showAlertConfirmNotifications({
          open: true,
          showConfirm: "showNotification",
          alertType: "success",
          alertContentKey: "Business function deleted successfully!",
        });
      })
      .catch((err) => {
        window.loading(false)
        if(err?.response?.data?.status === "0"){
        setAlertData({
          'open':true,
          "alertContentMsg": `${t(`translation:${err?.response?.data.err_msg}`)}`,
          'showConfirm':'showNotification',
          'alertType':'error',
          "autoHideDuration": 4000,
      })
      }
      });
  };

  const duplicateData = async (id) => {
    window.loading(true)
    await axios
      .post(`customRestApi/duplicateRestEndPoint/${id}/`, {
        withCredentials: true,
      })
      .then((res) => {
        if(res.status === 200){
          setAlertData({
            'open':true,
            "alertContentMsg": `${t(`translation:Duplicate object created successfully`)}`,
            'showConfirm':'showNotification',
            'alertType':'success',
            "autoHideDuration": 4000,
        })
        getAllData();
        }
        window.loading(false)
      })
      .catch((err) => {
        console.log(err.response, err.response.data)
        if(err?.response?.data){
          if(err?.response?.data?.status === -2){
            setAlertData({
              'open':true,
              "alertContentMsg": `${t(`translation:This business function has already been duplicated. Additional copies are not allowed.`)}`,
              'showConfirm':'showNotification',
              'alertType':'error',
              "autoHideDuration": 4000,
          })
          }else if(err?.response?.data?.status === -4){
            setAlertData({
              'open':true,
              "alertContentMsg": `${t(`translation:Duplicate of a copied function is restricted. Rename it first to proceed with duplication.`)}`,
              'showConfirm':'showNotification',
              'alertType':'error',
              "autoHideDuration": 4000,
          })
          }
        }
        window.loading(false)
      });
  };
  

  useEffect(() => {
    let published = availableFunction.filter((func) => func.status === "0")
    setPublishedFunction(published)

    let draft = availableFunction.filter((func) => func.status === "1" || func.status === "2")
    setDraftFunction(draft)
  }, [availableFunction])

  useEffect(() => {
    getAllData();
    let latestData = availableFunction.map((data) => {
      return { ...data, isHovered: false, isOptionOpen: false };
    });
    setAvailableFunction(latestData);
  }, []);

  useEffect(() => {
    if(updateData){
      getAllData()
      setUpdateData(false)
    }
  }, [updateData])

  const handleDelete = (e, id, name) => {
    if(id === prevValue[0]?.id){
      setIsAssociated(true)
    }else{
      setIsAssociated(false)
    }
    setDeleteId(id);
    setDeleteName(name);
    setDeleteDialogOpen(true);
  };

  const handleDuplicate = (e, id) => {
    duplicateData(id)
    setUpdated(true);
  };

  const handleFunctionDialogOpen = () => {
    let count = prevValue.filter((data) => data?.status === "0")
    if(count?.length > 0){
      setAlertData({
        'open':true,
        "alertContentMsg": `${t(`translation:Function_Already_Mapped`)}.`,
        'showConfirm':'showNotification',
        'alertType':'error',
        "autoHideDuration": 6000,
    })
    }else{
      setIsAssociate(false);
      setDraftData([])
      setFunctionDialogOpen(true)
    }
  }

  const getDraftData = async (endpoint, application, fId) => {
    window.loading(true)
    let restEndPoint = endpoint
    let applicationId = application
    let formId = fId
    let currentApplicationId = props.microFrontendData?.formDefId ? props.microFrontendData?.processId : props.microFrontendData?.applicationId
    let currentFormId = getFormId()
    let openState = props.microFrontendData?.statusType ? props.microFrontendData?.statusType : props.microFrontendData?.status.toLowerCase() === "draft" ? "L" : "R"
    let currentProcessName = props.microFrontendData.processName ? props.microFrontendData?.processName : " "
    let currentParentType = props.microFrontendData?.formDefId ? "P" : "A"
    

    await axios
      .get(`customRestApi/getRestEndPoint/${restEndPoint}/${applicationId}/${formId}/${currentApplicationId}/${currentFormId}/${openState}/${currentProcessName}/${currentParentType}`, {
        withCredentials: true,
      })
      .then((res) => {
        if(res?.data?.status === 1){
          setDataList(res?.data?.DataObject)
          setDataListOpen(true)
          window.loading(false)
          return false
        }else{
          setDraftData([res.data])
          setIsAssociate(false);
          setFunctionDialogOpen(true);
          window.loading(false)
          return true
        }
      })
      .catch((err) => {
        setAlertData({
          'open':true,
          "alertContentMsg": `${t(`translation:WIZARD_DROP_MESSAGE`)}`,
          'showConfirm':'showNotification',
          'alertType':'error',
          "autoHideDuration": 4000,
      })
      window.loading(false)
      return false
      });;
  };

  const getAssociatedData = async (endpoint, application, fId) => {
    window.loading(true)
    let restEndPoint = endpoint
    let applicationId = application
    let formId = fId
    let currentApplicationId = props.microFrontendData?.formDefId ? props.microFrontendData?.processId : props.microFrontendData?.applicationId
    let currentFormId = getFormId()
    let openState = props.microFrontendData?.statusType ? props.microFrontendData?.statusType : props.microFrontendData?.status.toLowerCase() === "draft" ? "L" : "R"
    let currentProcessName = props.microFrontendData.processName ? props.microFrontendData?.processName : " "
    let currentParentType = props.microFrontendData?.formDefId ? "P" : "A"
    

    await axios
      .get(`customRestApi/getRestEndPoint/${restEndPoint}/${applicationId}/${formId}/${currentApplicationId}/${currentFormId}/${openState}/${currentProcessName}/${currentParentType}`, {
        withCredentials: true,
      })
      .then((res) => {
        if(res?.data?.status === 1){
          setDataList(res?.data?.DataObject)
          setDataListOpen(true)
          window.loading(false)
          return false
        }else{
          setAssociateData([res.data]);
          setIsAssociate(true);
          setFunctionDialogOpen(true);
          window.loading(false)
          return true
        }
      })
      .catch((err) => {
        setAlertData({
          'open':true,
          "alertContentMsg": `${t(`translation:WIZARD_DROP_MESSAGE`)}`,
          'showConfirm':'showNotification',
          'alertType':'error',
          "autoHideDuration": 4000,
      })
      window.loading(false)
      });
  };
  
  const handleEdit = async(data) => {
    const res = await getDraftData(data?.restEndPoint, data?.parentId, data?.formId)
  };

  const handleAssociate = async(data, id) => {
    if(id){
      setIsAssociated(true)
    }else{
      setIsAssociated(false)
    }
    const res = await getAssociatedData(data?.restEndPoint, data?.parentId, data?.formId)
  };

  const handleMouseOver = (id) => {
    let updatedData = availableFunction?.map((data) => {
      if (data?.serialNo === id) {
        return { ...data, isHovered: true };
      } else {
        return { ...data, isHovered: false };
      }
    });
    setAvailableFunction(updatedData);
  };

  const handleMouseLeave = (id) => {
    let updatedData = availableFunction?.map((data) => {
      if (data?.serialNo === id) {
        return { ...data, isHovered: false };
      } else {
        return { ...data, isHovered: false };
      }
    });
    setAvailableFunction(updatedData);
  };

  const handleDeleteData = async() => {
    if (!deleteId) {
      props.updateBusinessFunctionData([]);
    } else {
      const res = await deleteData(deleteId)
    }
    setDeleteId(null);
    setDeleteName("");
    setFunctionDialogOpen(false)
    setDeleteDialogOpen(false);
    getAllData()
  };

  const handleDownload = async (e, appId, formId, restEndPoint, status, data, errorCallback = null) => {
    const launchpadKey=secureLocalStorage.getItem("Authorization") || JSON.parse(secureLocalStorage.getItem("launchpadKey"))?.token;
    var jwtToken=launchpadKey || undefined;
    let URL = `${window.apb_backend_app_context}/idesign/download/${appId}/${formId}/${restEndPoint}/${status}`
      try {
        var xmlhttp = new XMLHttpRequest();
        xmlhttp.open('GET', URL, true); //WdAudit URL changes
        xmlhttp.setRequestHeader('Content-Type', 'application/json;charset=UTF-8');
        xmlhttp.setRequestHeader('Authorization', jwtToken);
        xmlhttp.setRequestHeader('b-sid', localStorage.getItem('b-sid'));
        xmlhttp.responseType = 'blob';
        xmlhttp.send(JSON.stringify(data));
        xmlhttp.onreadystatechange = function () {
          if (this.readyState === 4 && this.status === 200) {
            var fileName = '';
            var disposition = xmlhttp.getResponseHeader('Content-Disposition');
            if (disposition && disposition.indexOf('attachment') !== -1) {
              var filenameRegex = /filename[^;=\n]*=((['"]).*?\2|[^;\n]*)/;
              var matches = filenameRegex.exec(disposition);
              if (matches !== null && matches[1]) fileName = matches[1].replace(/['"]/g, '');
            }
            var blob = this.response;
            if (window.navigator.msSaveOrOpenBlob) {
              window.navigator.msSaveBlob(blob, fileName);
            } else {
              var downloadLink = window.document.createElement('a');
              var contentTypeHeader = xmlhttp.getResponseHeader('Content-Type');
              if (contentTypeHeader.includes('application/json') && errorCallback) {
                blob.text().then(res => {
                  let parsedRes = JSON.parse(res);
                  if (parsedRes?.status?.maincode === '-1000' || parsedRes?.status?.maincode === '11') {
                    }
                  errorCallback(JSON.parse(res));
                });
                return false;
              }
              downloadLink.href = window.URL.createObjectURL(new Blob([blob], { type: contentTypeHeader }));
              fileName = decodeURIComponent(fileName);
              downloadLink.download = fileName;
              document.body.appendChild(downloadLink);
              downloadLink.click();
              document.body.removeChild(downloadLink);
            }
          }
        };
      } catch (err) {
        setAlertData({
          'open':true,
          "alertContentMsg": "Download Failed",
          'showConfirm':'showNotification',
          'alertType':'error',
          "autoHideDuration": 4000,
      })
      }
  }

  return (
    <div>
          <div className={`input ${styles.row}`}>
          {t("translation:Add_Business_Function")}
          <button
            onClick={() => handleFunctionDialogOpen()}
            className={styles.functionButton}
          >
            +
          </button>
        </div>
        {draftFunction.length > 0 && 
        <div style={{ height: "120px", overflowX: "auto" }}>
          {draftFunction?.map((data) => {
              return (
                <div
                  key={data.serialNo}
                  className={`input ${styles.row} ${styles.availableFunctionRow}`}
                  style={{
                    backgroundColor: data?.isHovered ? "#F5F5F8" : "#ffffff",
                  }}
                  onMouseOver={() => handleMouseOver(data.serialNo)}
                  onMouseLeave={() => handleMouseLeave(data.serialNo)}
                  id={data?.restEndPoint}
                >
                  <span style={{display: "flex"}}>
                    <span className={styles.functionLabel} title={data?.restEndPoint}>{data?.restEndPoint}</span>
                  </span>
                  {data?.isHovered ? (
                    <span
                      className={styles.hoveredDraftBusinessFuction}
                    >
                      <span title={t("translation:Edit Business Function")} onClick={() => handleEdit(data)}>
                        <Edit_Function />
                      </span>
                      <span title={t("translation:Delete Business Function")} onClick={(e) => handleDelete(e, data?.serialNo, data?.restEndPoint)}>
                        <DeleteOutline style={{color: "#D53D3D"}}/>
                      </span>
                      {data?.restEndPoint === prevValue[0]?.name && data?.status === "2" && 
                      <span title={t("translation:Download Business Function")} onClick={(e) => handleDownload(e, data?.parentId, Number(data?.formId), data?.restEndPoint, data?.status)}>
                        <Download_Function />
                      </span>
                      }
                    </span>
                  ) : (
                    <>
                    </>
                  )}
                </div>
              );
            })}
          </div>
          }

      <div className={`input ${styles.row}`}>
        <span>{t("translation:Available Business Function")} &nbsp;&nbsp;
        </span>
      </div>
      {publishedFunction.length > 0 ? (
        <>
          <div style={{ height: "150px", overflowX: "auto" }}>
            {publishedFunction?.map((data) => {
              return (
                <div
                  key={data.serialNo}
                  className={`input ${styles.row} ${styles.availableFunctionRow}`}
                  style={{
                    backgroundColor: data?.isHovered ? "#F5F5F8" : "#ffffff",
                  }}
                  onMouseOver={() => handleMouseOver(data.serialNo)}
                  onMouseLeave={() => handleMouseLeave(data.serialNo)}
                  id={data?.restEndPoint}
                >
                  <span style={{display: "flex"}}>
                    <span className={styles.functionLabel} title={data?.restEndPoint}>{data?.restEndPoint}</span>
                    {data?.restEndPoint === prevValue[0]?.name ? (
                      <span title={t("translation: Linked Business Function")}>
                      <CheckCircle
                        style={{ color: "green", margin: "0px 10px", cursor: "default" }}
                      />
                      </span>
                    ) : null}
                  </span>
                  {data?.isHovered ? (
                    <span
                      className={styles.hoveredBusinessFuction}
                    >
                      <span title={t("translation:Download Business Function")} onClick={(e) => handleDownload(e, data?.parentId, Number(data?.formId), data?.restEndPoint, data?.status)}>
                        <Download_Function />
                      </span>
                      <span title={t("translation:Duplicate Business Function")} onClick={(e) => handleDuplicate(e, data?.serialNo)}>
                        <Copy_Function />
                      </span>
                      {data?.restEndPoint === prevValue[0]?.name ? (
                      <span title={t("translation:De-Link Business Function")} onClick={(e) => handleAssociate(data, true)}>
                        <img
                          src={deLink}
                          style={{ cursor: "pointer", color: "#D53D3D"}}
                          alt="Delink Icon"
                        />
                      </span>
                      ) :
                      (
                      <span title={t("translation:Link Business Function")} onClick={() => handleAssociate(data, false)}>
                        <LinkIcon style={{width: "20px", height: "20px"}} />
                      </span>
                      )}
                    </span>
                  ) : null }
                </div>
              );
            })}
          </div>
        </>
      ) : 
      <div style={{height: "50px",textAlign: "center", margin: "auto"}}>
        <span>
        {t("translation:No_Business_Function_Available")}
        </span>
      </div>
    }

      {functionDialogOpen ? (
        <BusinessFunctionBackdrop
          functionDialogOpen={functionDialogOpen}
          setFunctionDialogOpen={setFunctionDialogOpen}
          prevValue={isAssociate ? associateData : draftData}
          defaultValue={defaultValue}
          isAllFilled={isAllFilled}
          setIsAllFilled={setIsAllFilled}
          updateBusinessFunctionData={(businessJson) =>
            props.updateBusinessFunctionData(businessJson)
          }
          microFrontendData={props.microFrontendData}
          isAssociate={isAssociate}
          associateData={associateData}
          handleDeleteData={handleDeleteData}
          isAssociated = {isAssociated}
          deleteName={deleteName}
          getAllData={() => getAllData()}
          setUpdateData={setUpdateData}
      ></BusinessFunctionBackdrop>
      ) : null}

      {deleteDialogOpen ? (
        <DeleteConfirmation
          deleteDialogOpen={deleteDialogOpen}
          setDeleteDialogOpen={setDeleteDialogOpen}
          handleDeleteData={handleDeleteData}
          deleteName={deleteName}
        />
      ) : null}
      
      {dataListOpen ? (
        <ShowDataList 
        dataListOpen={dataListOpen}
        setDataListOpen={setDataListOpen}
        dataList={dataList}
        />
      ) : null}

      {
        alertData.open === true &&
          <CustomizedAlert alertData={alertData}/>
      }
    </div>
  );
};

export default BusinessFunction;
