import Dialog from "@material-ui/core/Dialog";
import { withStyles, makeStyles } from "@material-ui/core/styles";
import MuiDialogActions from "@material-ui/core/DialogActions";
import MuiDialogContent from "@material-ui/core/DialogContent";
import uuid from "react-uuid";
import { useRef, useState } from "react";
import axios from "../../axios";
import styles from "./Functions.module.css";
import ReusableBusinessFunction from "./ReusableBusinessFunction";
import { getFormId } from "../../components/IFormDesignerLayout/IFormDesignerLogic";
import { useTranslation } from "react-i18next";
import { GetStateFromData } from "../../components/CustomHooks/CustomHooks";

const useStyles = makeStyles((theme) => ({
  table: {
    minWidth: 650,
  },
  tooltip: {
    maxWidth: "160px",
    background: "#FFFFFF 0% 0% no-repeat padding-box",
    color: "black",
    border: "#70707075 1px solid",
    boxShadow: "0px 3px 6px #00000029",
    opacity: 1,
    fontSize: 12,
  },
  arrow: {
    "&:before": {
      border: "1px solid #70707075",
    },
    color: "white",
    fontSize: 15,
  },
  dialogPaper: {
    minWidth: "950px",
    borderRadius: "2px",
    overflowX: "hidden",
  },
  dialogHeader: {
    fontWeight: "bold",
    padding: "10px",
    fontSize: "14px",
  },
  dialogSubHeading: {
    width: "100%",
    height: "1px",
    backgroundColor: "#F0F0F0",
    fontSize: "12px",
    fontWeight: "bold",
    border: "1px solid #d7d7d7",
  },
  dialogFooter: {
    position: "relative",
    "justify-content": "end",
    padding: "10px",
    bottom: 0,
    width: "100%",
    "background-color": "#f0f0f0",
  },
  dialogContent: {
    width: "100%",
    justifyContent: "space-between",
    fontFamily: "Open Sans !important",
    fontSize: "14px !important",
  },
  dialogContentMethod: {
    width: "58%",
    justifyContent: "space-between",
    fontFamily: "Open Sans !important",
    fontSize: "14px !important",
    display: "flex",
  },
  cancelButton: {
    cursor: "pointer",
    "border-radius": "0px",
    border: "1px solid grey",
    width: "60px",
    height: "30px",
    margin: "0px 10px",
  },
  publishButton: {
    cursor: "pointer",
    "border-radius": "0px",
    border: "1px solid blue",
    width: "60px",
    height: "30px",
    margin: "0px 10px",
  },
  inputField: {
    borderRadius: "10px",
    width: "350px",
  },
  inputBox: {
    marginLeft: "10px",
    borderRadius: "10px",
    width: "430px",
    display: "block",
    overflowY: "auto",
  },
  bodyTab1: {
    textAlign:
      window?.depsatkur_configs?.selectedLanguage === "ar" || window?.depsatkur_configs?.selectedLanguage === "ar-SA"
        ? "right"
        : "left",
    font: "normal normal 600 12px/17px Open Sans;",
    letterSpacing: "0px",
    color: "#000000",
    opacity: 1,
  },
  tab: {
    marginRight:
      window?.depsatkur_configs?.selectedLanguage === "ar" || window?.depsatkur_configs?.selectedLanguage === "ar-SA"
        ? "0px"
        : "40px",
    marginLeft:
      window?.depsatkur_configs?.selectedLanguage === "ar" || window?.depsatkur_configs?.selectedLanguage === "ar-SA"
        ? "40px"
        : "0px",
    verticalAlign: "middle",
    cursor: "pointer",
  },
  activeTab: {
    color: "var(--brand_color2)",
    paddingBottom: "7px",
    borderBottom: "3px solid var(--brand_color2)",
    marginRight:
      window?.depsatkur_configs?.selectedLanguage === "ar" || window?.depsatkur_configs?.selectedLanguage === "ar-SA"
        ? "0px"
        : "40px",
    marginLeft:
      window?.depsatkur_configs?.selectedLanguage === "ar" || window?.depsatkur_configs?.selectedLanguage === "ar-SA"
        ? "40px"
        : "0px",
    verticalAlign: "middle",
    cursor: "pointer",
  },
}));
const DialogContent = withStyles((theme) => ({
  root: {
    padding: 0,
    fontFamily: "Open Sans",
    fontSize: "14px",
    paddingTop: "0px !important",
  },
}))(MuiDialogContent);
const DialogActions = withStyles((theme) => ({
  root: {
    margin: 0,
    padding: 0,
  },
}))(MuiDialogActions);

const BusinessFunctionBackdrop = (props) => {
  const functionName = useRef(props?.prevValue[0]?.restEndPoint || "");
  const endpointURL = useRef(props?.prevValue[0]?.url || "");
  const type = useRef("post");
  const [inputDataObjects, setInputDataObjects] = useState(
    props?.prevValue[0]?.input || []
  );
  const [outputDataObjects, setOutputDataObjects] = useState(
    props?.prevValue[0]?.output || []
  );
  const [isDefineParameters, setIsDefineParameters] = useState(false);

  const { t } = useTranslation(["styles", "translation"]);
  const translationStyles1 = window?.depsatkur_configs?.selectedLanguage === "ar" || window?.depsatkur_configs?.selectedLanguage === "ar-SA";

  const contexts = GetStateFromData();
  const { activePage, childComp, showAlertConfirmNotifications } = contexts.contexts;

  const pushData = async (payload) => {
    window.loading(true)
    await axios
      .post("customRestApi/saveRestEndPoint", payload, {
        withCredentials: true,
      })
      .then((res) => {
        showAlertConfirmNotifications({
          open: true,
          showConfirm: "showNotification",
          alertType: "success",
          alertContentKey: `${t("translation:Data saved successfully")}`,
        });
        window.loading(false)
        return res.data
      })
      .catch((err) => {
        window.loading(false)
        showAlertConfirmNotifications({
          open: true,
          showConfirm: "showNotification",
          alertType: "error",
          alertContentKey: `${t("translation:Failed to save data")}`,
        });
      });
  };

  const handleSaveData = async(e, stat) => {
    const payload = {
      status: stat,
      url: endpointURL.current,
      type: type.current.toUpperCase(),
      endPoint: functionName.current,
      input: inputDataObjects,
      output: outputDataObjects,
      parentId: props.microFrontendData?.formDefId
        ? props.microFrontendData?.processId
        : props.microFrontendData?.applicationId,
      parentType: props.microFrontendData?.formDefId ? "P" : "A",
      formId: getFormId().toString(),
      formType: props.microFrontendData?.formDefId ? "form" : (childComp === "application" || activePage) ? "interfaces" : "widgets",
      processStatus: props?.microFrontendData?.processName ? props?.microFrontendData?.processName : "",
    };

    props.setFunctionDialogOpen(false);
    return pushData(payload);
  };

  const handleSave = (e) => {
    const status = "1"
    handleSaveData(e, status).then(() => props?.setUpdateData(true))
  }

  const handleAssociateData = async(e) => {
      const obj = {
        id: uuid(),
        name: functionName.current,
        url: endpointURL.current,
        type: type.current.toUpperCase(),
        input: inputDataObjects,
        output: outputDataObjects,
        status: "0",
      };
      props.setFunctionDialogOpen(false);
      props.updateBusinessFunctionData([obj]);
      props?.getAllData()

    return true;
  };

  const handleAssociate = (e) => {
    handleAssociateData(e).then(() => props?.setUpdateData(true))
  }

  const createRestApi = async (stat) => {
    window.loading(true)
    const obj = {
      id: uuid(),
      name: functionName.current,
      url: endpointURL.current,
      type: type.current.toUpperCase(),
      input: inputDataObjects,
      output: outputDataObjects,
      status: stat,
    };
    const payload = {
      controlName: functionName.current,
      applicationName: props?.microFrontendData?.applicationName
    };
    await axios
      .post("idesign/createRestAPI", payload, { withCredentials: true })
      .then((res) => {
        props.updateBusinessFunctionData([obj]);
        showAlertConfirmNotifications({
          open: true,
          showConfirm: "showNotification",
          alertType: "success",
          alertContentKey: `${t("translation:Data published successfully")}`,
        });
        window.loading(false)
      })
      .catch((err) => {
        showAlertConfirmNotifications({
          open: true,
          showConfirm: "showNotification",
          alertType: "error",
          alertContentKey: `${t("translation:Failed to publish data")}`,
        });
        window.loading(false)
      });

      return true;
  };

  const handleCreateRestApi = (e) => {
    const status = "0"
    const payload = {
      status: status,
      url: endpointURL.current,
      type: type.current.toUpperCase(),
      endPoint: functionName.current,
      input: inputDataObjects,
      output: outputDataObjects,
      parentId: props.microFrontendData?.processId
        ? props.microFrontendData?.processId
        : props.microFrontendData?.applicationId,
      parentType: props.microFrontendData?.formDefId ? "P" : "A",
      formId: getFormId().toString(),
      formType: props.microFrontendData?.formDefId ? "form" : (childComp === "application" || activePage) ? "interfaces" : "widgets",
      processStatus: props?.microFrontendData?.processName ? props?.microFrontendData?.processName : "",
    };
    pushData(payload).then(() => { createRestApi(status).then(() => { props?.getAllData() } ) })
    props.setFunctionDialogOpen(false);
  };

  const handleAssociateAPI = (e) => {
    const status = "2"
    const payload = {
      status: status,
      url: endpointURL.current,
      type: type.current.toUpperCase(),
      endPoint: functionName.current,
      input: inputDataObjects,
      output: outputDataObjects,
      parentId: props.microFrontendData?.processId
        ? props.microFrontendData?.processId
        : props.microFrontendData?.applicationId,
      parentType: props.microFrontendData?.formDefId ? "P" : "A",
      formId: getFormId().toString(),
      formType: props.microFrontendData?.formDefId ? "form" : (childComp === "application" || activePage) ? "interfaces" : "widgets",
      processStatus: props?.microFrontendData?.processName ? props?.microFrontendData?.processName : "",
    };
    pushData(payload).then(() => { createRestApi(status).then(() => { props?.getAllData() } ) })
    props.setFunctionDialogOpen(false);
  }

  const classes = useStyles();

  return (
    <Dialog
      open={props.functionDialogOpen}
      classes={{ paper: classes.dialogPaper }}
      className={"formBuilder_main_app"}
    >
      <DialogContent>
        <ReusableBusinessFunction
          functionName={functionName}
          endpointURL={endpointURL}
          inputDataObjects={inputDataObjects}
          setInputDataObjects={setInputDataObjects}
          outputDataObjects={outputDataObjects}
          setOutputDataObjects={setOutputDataObjects}
          isDefineParameters={isDefineParameters}
          setIsDefineParameters={setIsDefineParameters}
          prevValue={props?.prevValue}
          microFrontendData={props.microFrontendData}
        />
      </DialogContent>

      <DialogActions>
        {!props?.isAssociate ? (
          <div className={`${classes.dialogFooter}`} style={translationStyles1 ? null : {display: "flex"}}>
              <button
                className={`${classes.cancelButton} tertiary`}
                onClick={() => props.setFunctionDialogOpen(false)}
              >
                {t("translation:Cancel")}
              </button>
              <button
                className={styles.saveBtn}
                onClick={(e) => handleSave(e)}
                disabled={!isDefineParameters}
              >
                {t("translation:Save")}
              </button>
              <button
                className={styles.saveBtn}
                onClick={(e) => handleAssociateAPI(e)}
                disabled={ 
                  (isDefineParameters && inputDataObjects.length > 0 && outputDataObjects.length > 0)
                   ? false
                    : true
                }
              >
                Associate
              </button>
              <button
              className={`${classes.publishButton} tertiary`}
              onClick={(e) => handleCreateRestApi(e)}
              disabled={ 
                (isDefineParameters && inputDataObjects.length > 0 && outputDataObjects.length > 0)
                 ? false
                  : true
              }
            >
              {t("translation:Publish")}
            </button>
          </div>
        ) : (
          <div className={`${classes.dialogFooter}`} style={translationStyles1 ? null : {display: "flex"}}>
              <button
                className={`${classes.cancelButton} tertiary`}
                onClick={() => props.setFunctionDialogOpen(false)}
              >
                {t("translation:Cancel")}
              </button>
              {props?.isAssociated ? 
              <button
                className={styles.saveBtn}
                onClick={(e) => props?.handleDeleteData(e)}
              >
                {t("translation:De-Link")}
              </button>
              :
              <button
                className={styles.saveBtn}
                onClick={(e) => handleAssociate(e)}
                disabled={
                  (isDefineParameters && inputDataObjects.length > 0 && outputDataObjects.length > 0)
                    ? false
                    : true
                }
              >
                {t("translation:Link")}
              </button>
              }
              
            </div>
        )}
      </DialogActions>
    </Dialog>
  );
};

export default BusinessFunctionBackdrop;
