import React, { useEffect, useState, useRef } from "react";
import Dialog from "@material-ui/core/Dialog";
import { withStyles, makeStyles } from "@material-ui/core/styles";
import MuiDialogActions from "@material-ui/core/DialogActions";
import MuiDialogContent from "@material-ui/core/DialogContent";
import { useTranslation } from "react-i18next";

const useStyles = makeStyles((theme) => ({
  root: {
    padding: 0,
    margin: "4px",
    border: "1px solid #C4C4C4",
    "& .MuiChip-root": {
      borderRadius: 2,
      width: "35%",
      height: "25px",
      backgroundColor: "#F0F0F0",
    },
    "& .MuiButtonBase-root": {
      margin: "0 !important",
    },
    "& .MuiInputBase-input": {
      border: "none !important",
      width: "75% !important",
      flexGrow: "0",
    },
    "& .MuiAutocomplete-endAdornment": {
      right:
        window?.depsatkur_configs?.selectedLanguage === "ar" || window?.depsatkur_configs?.selectedLanguage === "ar-SA"
          ? "unset"
          : null,
      left:
        window?.depsatkur_configs?.selectedLanguage === "ar" || window?.depsatkur_configs?.selectedLanguage === "ar-SA"
          ? "-183px"
          : null,
    },
    "& .MuiChip-deleteIcon": {
      marginRight:
        window?.depsatkur_configs?.selectedLanguage === "ar" || window?.depsatkur_configs?.selectedLanguage === "ar-SA"
          ? "unset"
          : null,
      marginLeft:
        window?.depsatkur_configs?.selectedLanguage === "ar" || window?.depsatkur_configs?.selectedLanguage === "ar-SA"
          ? "5px"
          : null,
    },
  },
  tooltip: {
    maxWidth: "160px",
    background: "#FFFFFF 0% 0% no-repeat padding-box",
    color: "black",
    border: "#70707075 1px solid",
    boxShadow: "0px 3px 6px #00000029",
    opacity: 1,
    fontSize: 12,
  },
  dialogPaper: {
    minHeight: "200px",
    minWidth: "500px",
    borderRadius: "2px",
    overflowX: "hidden",
  },
  dialogHeader: {
    fontWeight: "bold",
    padding: "10px 15px",
    fontSize: "14px",
  },
  dialogFooter: {
    position: "relative",
    display: "flex",
    "justify-content": "end",
    // padding: "10px",
    bottom: 0,
    width: "100%",
    "background-color": "#f0f0f0",
  },
  dialogContent: {
    width: "100%",
    justifyContent: "space-between",
    fontFamily: "Open Sans !important",
    fontSize: "14px !important",
  },
  dialogSubHeading: {
    width: "100%",
    height: "1px",
    backgroundColor: "#F0F0F0",
    fontSize: "12px",
    fontWeight: "bold",
    border: "1px solid #d7d7d7",
  },
  cancelButton: {
    height: "32px !important",
    background: "#FFFFFF 0% 0% no-repeat padding-box !important",
    fontFamily: "Open Sans !important",
    fontSize: "12px !important",
    fontWeight: "600 !important",
    color: "#606060 !important",
    border: "1px solid #C4C4C4 !important",
    textTransform: "none !important",
    borderRadius: "2px !important",
    opacity: 1,
    cursor: "pointer !important",
    width: "60px !important",
  },
  deleteButton: {
    height: "32px !important",
    // background: '#D53D3D 0% 0% no-repeat padding-box',
    background: `#D53D3D 0% 0% no-repeat padding-box !important`,
    fontFamily: "Open Sans !important",
    fontSize: "12px !important",
    fontWeight: "600 !important",
    border: "1px solid #C4C4C4 !important",
    color: "#FFFFFF !important",
    textTransform: "none !important",
    borderRadius: "2px !important",
    opacity: 1,
    cursor: "pointer !important",
    width: "60px !important",
  }
}));
const DialogContent = withStyles((theme) => ({
  root: {
    padding: 0,
    fontFamily: "Open Sans",
    fontSize: "14px",
    // textAlign: "center",
    margin: "20px 15px",
  },
}))(MuiDialogContent);
const DialogActions = withStyles((theme) => ({
  root: {
    margin: 0,
    padding: 0,
  },
}))(MuiDialogActions);

const DeleteConfirmation = (props) => {
  const classes = useStyles();

  const { t } = useTranslation(["styles", "translation"]);

  return (
    <Dialog
      open={props.deleteDialogOpen}
      classes={{ paper: classes.dialogPaper }}
      className={"formBuilder_main_app"}
    >
      {/* heading section */}
      <div>
        <div className={classes.dialogHeader}>
          <span>{t("translation:Delete Confirmation")}</span>
        </div>
      </div>
      <div className={classes.dialogSubHeading}></div>
      <DialogContent>
      <span>
        {t("translation:Are you sure you want to delete the business function")},{" "}
        <b>"{props?.deleteName}"</b>? {t("translation:Once it's deleted, all the data it contains will be lost.")}
      </span>
      </DialogContent>
      {/* footer section */}

      <DialogActions>
        <div className={`${classes.dialogFooter}`}>
          <button
            className={`${classes.cancelButton} tertiary`}
            onClick={() => props.setDeleteDialogOpen(false)}
          >
            {t("translation:Cancel")}
          </button>
          <button
            className={`${classes.deleteButton} danger`}
            onClick={(e) => props.handleDeleteData(e)}
          >
            {t("translation:Delete")}
          </button>
        </div>
      </DialogActions>
    </Dialog>
  );
};

export default DeleteConfirmation;
